var graceChecker = require("./graceChecker.js");
import {
	requestWB as uniRequest,
	requestZT as ztRequest
} from 'uni-request';
export default {
	name: 'added',
	props: {
		showchose: {
			type: Boolean,
			default () {
				return false;
			}
		},
		tid: {
			// 售卖产品id
			type: String,
			default () {
				return '';
			}
		},
		openId: {
			// userId
			type: String,
			default () {
				return '';
			}
		},
		list: {
			type: Array,
			default () {
				return [];
			}
		},
		arr: {
			type: Array,
			default () {
				return [];
			}
		},
		neednub: {
			type: Number
		}
	},
	data() {
		return {
			show: this.showchose,
			newpages: false,
			openid: '',
			type: '',
			name: null,
			idcard: null,
			phone: null,
			email: null,
			lists: [],
			newopen: false,
			choese: false,
			idx: 999,
			externalId: '',
			openCode: ''
		};
	},
	computed: {
		newlist() {
			let arr = this.list
			if (this.arr.length == 0) {
				for (let i = 0; i < arr.length; i++) {
					arr[i].showbtn = false
				}
			}
			if (this.newopen) {
				arr = this.lists
			}
			return arr
		},
		narr() {
			return this.arr
		}

	},
	methods: {
		tijiao() {
			let newarr = []
			for (let i = 0; i < this.narr.length; i++) {
				newarr.push(this.narr[i].openCode)
			}

			this.openCode = newarr.toString();
			console.log(this.openCode, 9098)
			this.$emit('chosemsg', this.name, this.openCode, this.idx, this.narr);
			this.show = false;
			this.$emit('handleShowChose', this.show);
			// this.choese=false;
			// console.log(this.name)
		},
		chose(name, externalId, index, openCode, showbtn) {
			let newarr = this.newlist
			console.log(this.newlist, showbtn, 'list')
			// if(this.narr.length<this.neednub){
			this.newlist[index].showbtn = !this.newlist[index].showbtn
			if (newarr[index].showbtn) {
				this.narr.push({
					'openCode': openCode,
					'name': name
				})
			} else {
				for (let a = 0; a < this.narr.length; a++) {
					if (this.narr[a].openCode == newarr[index].openCode) {
						this.narr.splice(a, 1)
					}
				}
			}





		},
		closechose() {
			this.show = false;

			this.$emit('handleShowChose', this.show);
		},

		close() {
			this.newpages = false
			this.formReset()
		},
		newpage(i, openid, name, idcard, phone, email, openCode) {
			if (i === 1) {
				// console.log('编辑信息')			
				this.name = name
				this.idcard = idcard
				this.phone = phone
				this.email = email
				this.openid = openid
				this.type = 1,
					this.openCode = openCode
			} else {
				this.type = 0
				this.name = ''
				this.idcard = ''
				this.phone = ''
				this.email = ''
			}
			this.newpages = true;
		},
		formReset: function(e) {
			// console.log("清空数据")
			this.chosen = ''
		},
		formSubmit: function(e) {
			//将下列代码加入到对应的检查位置
			//定义表单规则
			var rule = [{
					name: "nickname",
					checkType: "string",
					checkRule: "1,6",
					errorMsg: "请输入姓名"
				},
				{
					name: "phone",
					checkType: "phoneno",
					checkRule: "",
					errorMsg: "请输入正确的手机号"
				},
				{
					name: "idCard",
					checkType: "string",
					checkRule: "15,18",
					errorMsg: "请输入正确的身份证号码"
				},

			];
			//进行表单检查
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			let _this = this
			const value = uni.getStorageSync('openId')

			if (checkRes) {
				// uni.showToast({title:"验证通过!", icon:"none"});
				// console.log(formData)
				if (this.type === 0) {
					ztRequest.post('/api/public/order/saveUserManage', {
							name: formData.nickname,
							openId: value,
							phone: formData.phone,
							idCard: formData.idCard,
							email: formData.email
						})
						.then(function(res) {
							// _this.list=	res.data.content	
							_this.$emit('shuaxin', true, _this.narr)

							// console.log('新增', 11);
						})
						.catch(function(error) {
							console.log(error);
						});
					this.formReset()
					this.newpages = false
				} else {
					ztRequest.post('/api/public/order/updateUserManage', {
							name: formData.nickname,
							phone: formData.phone,
							idCard: formData.idCard,
							email: formData.email,
							openCode: _this.openCode
						})
						.then(function(res) {
							// _this.list=	res.data.content	
							_this.$emit('shuaxin', true, _this.narr)
						})
						.catch(function(error) {
							console.log(error);
						});
				}
				this.formReset()
				this.newpages = false

			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		}

	}
};
